"use client";

import { useState } from "react";
import Calendar from "react-calendar";
import "react-calendar/dist/Calendar.css";

type ValuePiece = Date | null;
type Value = ValuePiece | [ValuePiece, ValuePiece];

const events = [
  { date: "2025-01-10", title: "Staff Meeting" },
  { date: "2025-01-15", title: "Mid-Term Test" },
  { date: "2025-01-20", title: "PTA Meeting" },
];

export default function TinyCalendar() {
  const [value, onChange] = useState<Value>(new Date());

  const selectedDate =
    value instanceof Date ? value.toISOString().split("T")[0] : null;

  const dayEvents = events.filter(e => e.date === selectedDate);

  return (
    <div className="rounded-lg bg-white p-2 shadow-sm dark:bg-gray-900">

      {/* Header */}
      <h3 className="mb-1 text-xs font-semibold text-gray-700 dark:text-gray-300">
        Calendar
      </h3>

      {/* Calendar */}
      <Calendar
        onChange={onChange}
        value={value}
        showNeighboringMonth={false}
        showWeekNumbers={false}
        className="tiny-calendar"
        tileClassName={({ date }) => {
          const hasEvent = events.some(
            e => e.date === date.toISOString().split("T")[0]
          );
          return hasEvent
            ? "relative after:absolute after:bottom-[2px] after:left-1/2 after:h-[4px] after:w-[4px] after:-translate-x-1/2 after:rounded-full after:bg-purple-500"
            : null;
        }}
      />

      {/* Events (Ultra Compact) */}
      {dayEvents.length > 0 && (
        <div className="mt-1 text-[10px] text-gray-600 dark:text-gray-400 truncate">
          • {dayEvents[0].title}
        </div>
      )}
    </div>
  );
}
